// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import './axios/index'
import router from './router'
import 'lib-flexible'
import store from './store/store'
import axios from './axios/index'
import 'babel-polyfill'
import Es6Promise from 'es6-promise'
import './assets/css/index.css'
require('es6-promise').polyfill()
Es6Promise.polyfill()
Vue.config.productionTip = false
Vue.prototype.$hu = new Vue();

router.beforeEach((to, from, next) => {
  // if (to.query.authtoken && to.query.authuid) {
  //   localStorage.setItem('authtoken', to.query.authtoken)
  //   localStorage.setItem('authuid', to.query.authuid)
  // }
  // if (store.state.userInfo === null) {
  //   if(to.path == '/login' || to.path == '/reset' || to.path == '/register'){
  //     next()
  //   } else {
  //     next('/login')
  //   }
  // } else {

  if (to.query.hu_title) {
    document.title = to.query.hu_title   //  动态设置标题
  } else {
    document.title = to.meta.title       //  常规设置 标题
  }
  next()
  // }
})

/* eslint-disable no-new */
const vue= new Vue({
  el: '#app',
  store,
  router,
  axios,
  components: { App },
  template: '<App/>'
})

export default vue.$store


