<template>
  <div>
    <router-view style='paddingBottom:50px'></router-view>
    <van-tabbar 
    v-model="active"
    active-color="#15BDDB"
    inactive-color="#999"
    v-if="$route.query.type_user!='user'">
      <van-tabbar-item to="/saoma">
        <span>扫码充电</span>
        <img slot="icon"
             class="icon"
             slot-scope="props"
             :src="props.active ? home.active : home.normal">
      </van-tabbar-item>
      <van-tabbar-item to="/mall">
        <span>好物商场</span>
        <img slot="icon"
             class="icon"
             slot-scope="props"
             :src="props.active ? cyclopedia.active : cyclopedia.normal">
      </van-tabbar-item>
      <van-tabbar-item to="/user">
        <span>个人中心</span>
        <img slot="icon" 
             slot-scope="props"
             class="icon"
             :src="props.active ? user.active : user.normal">
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Tabbar, TabbarItem, CellGroup } from 'vant';
import home from '@/assets/images/navbar/tab_ic_home_nor@2x.png'
import home1 from '@/assets/images/navbar/tab_ic_home_sel@2x.png'
import cyclopedia from '@/assets/images/navbar/tab_ic_store_nor@2x.png'
import cyclopedia1 from '@/assets/images/navbar/tab_ic_store_sel@2x.png'
import user from '@/assets/images/navbar/tab_ic_personal_nor@2x.png'
import user1 from '@/assets/images/navbar/tab_ic_personal_sel@2x.png'

export default {
  components: {
    'vanTabbar': Tabbar,
    'vanTabbarItem': TabbarItem

  },
  data() {
    return {
      active: 0,
      home: {
        normal: home,
        active: home1
      },
      cyclopedia: {
        normal: cyclopedia,
        active: cyclopedia1
      },
      user: {
        normal: user,
        active: user1
      },
      router_:null
    }
  },
  created() {
    console.log(this.$route.name)
    switch(this.$route.name){

      case 'saoma': 
        this.active=0;
        break;

      case "mall": 
        this.active=1;
        break;

      case "user": 
        this.active=2;
        break;

      default:break;
    }
    this.change(this.$route.name);
  },
  methods: {
    change(name) {
      let obj = {
        saoma: 0,
        mall: 1,
        user: 2,
      }
      this.active = obj[name] 
    }
  },
  watch: {
    $route(to, from) {
      this.change(to.name)
    }
  },
}
</script>

<style lang="scss" scoped>
.van-tabbar {
  height: 104px;
}
.icon{
  width:20pt;
  height:20pt;
}
/deep/ .van-tabbar{
  border-top:1px solid #eee;
}
</style>



