import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect:'/mall',
      component: () => import('../components/home/index.vue'),
      meta: {
        title: '商城首页'
      },
    },
    {
      path: '/zong_order',
      name: 'zong_order',
      component: () => import('../components/mysj/zong_order.vue'),
      meta: {
        title: '总订单'
      },
    },
    {
      path: '/myshebei',
      name: 'myshebei',
      component: () => import('../components/mysj/myshebei.vue'),
      meta: {
        title: '我的设备'
      },
    },
    {
      path: '/bdshebei',
      name: 'bdshebei',
      component: () => import('../components/mysj/bdshebei.vue'),
      meta: {
        title: '绑定设备'
      },
    },
    {
      path: '/shouru',
      name: 'shouru',
      component: () => import('../components/mysj/shouru.vue'),
      meta: {
        title: '收入统计'
      },
    },
    {
      path: '/add_daili',
      name: 'add_daili',
      component: () => import('../components/mysj/add_daili.vue'),
      meta: {
        title: '添加代理'
      },
    },
    {
      path: '/add_jiudian',
      name: 'add_jiudian',
      component: () => import('../components/mysj/add_jiudian.vue'),
      meta: {
        title: '添加酒店'
      },
    },
    {
      path: '/my_zujiezhong',
      name: 'my_zujiezhong',
      component: () => import('../components/mysj/my_zujiezhong.vue'),
      meta: {
        title: '租借中'
      },
    },
    {
      path: '/dailishenqing',
      name: 'dailishenqing',
      component: () => import('../components/mysj/dailishenqing.vue'),
      meta: {
        title: '代理申请'
      },
    },
    {
      path: '/sj_zhongxin',
      name: 'sj_zhongxin',
      component: () => import('../components/mysj/sj_zhongxin.vue'),
      meta: {
        title: '商家中心'
      },
    },
    {
      path: '/mall',
      name: 'mall',
      component: () => import('../components/home/index.vue'),
      meta: {
        title: '商城首页'
      },
    },
    {
      path: '/lingqian',
      name: 'lingqian',
      component: () => import('../components/mymoney/lingqian.vue'),
      meta: {
        title: '我的零钱'
      },
    },
    {
      path: '/myorder',
      name: 'myorder',
      component: () => import('../components/my/myOrder.vue'),
      meta: {
        title: '我的订单'
      },
    },
    {
      path: '/user',
      name: 'user',
      component: () => import('../components/my/user.vue'),
      meta: {
        title: '个人中心'
      }
    },
    {
      path: '/dizhi',
      name: 'dizhi',
      component: () => import('../components/my/shouhuodizhi.vue'),
      meta: {
        title: '收货地址'
      }
    },
    {
      path: '/address',
      name: 'address',
      component: () => import('../components/my/address.vue'),
      meta: {
        title: '收货地址'
      }
    },
    {
      path: '/huanbao',
      name: 'huanbao',
      component: () => import('../components/my/huanbao.vue'),
      meta: {
        title: '环保电量'
      }
    },
    {
      path: '/myteam',
      name: 'myteam',
      component: () => import('../components/my/myteam.vue'),
      meta: {
        title: '我的团队'
      }
    },
    {
      path: '/goodsDetail',
      name: 'goodsDetail',
      component: () => import('../components/details/details.vue'),
      meta: {
        title: '商品详情'
      }
    },
    {
      path: '/fenlei',
      name: 'fenlei',
      component: () => import('../components/fenlei.vue'),
      meta: {
        title: '商品分类'
      }
    },
    {
      path: '/erji',
      name: 'erji',
      component: () => import('../components/erji.vue'),
      meta: {
        title: '二级分类'
      }
    },
    {
      path: '/uporder',
      name: 'uporder',
      component: () => import('../components/uporder.vue'),
      meta: {
        title: '提交订单'
      }
    },
    {
      path: '/cart',
      name: 'cart',
      component: () => import('../components/cart.vue'),
      meta: {
        title: '购物车'
      }
    },
    {
      path: '/search',
      name: 'search',
      component: () => import('../components/search.vue'),
      meta: {
        title: '搜索'
      }
    },
    {
      path: '/search_detail',
      name: 'search_detail',
      component: () => import('../components/search_detail.vue'),
      meta: {
        title: '搜索详情'
      }
    },
    {
      path: '/saoma',
      name: 'saoma',
      component: () => import('../components/my/saoma.vue'),
      meta: {
        title: '扫码充电'
      }
    },
    {
      path: '/duihuan',
      name: 'duihuan',
      component: () => import('../components/my/duihuan.vue'),
      meta: {
        title: "立即充电"
      }
    },
    {
      path: '/order_detail',
      name: 'order_detail',
      component: () => import('../components/my/order_detail.vue'),
      meta: {
        title: '订单详情'
      }
    },
    {
      path: '/help',
      name: 'help',
      component: () => import('../components/my/help.vue'),
      meta: {
        title: '帮助中心'
      }
    },
    {
      path: '/tixian',
      name: 'tixian',
      component: () => import('../components/mymoney/tixian.vue'),
      meta: {
        title: '申请提现'
      }
    },
    {
      path: '/nologin',
      name: 'nologin',
      component: () => import('../components/my/nologin.vue'),
      meta: {
        title: '授权关注公众号'
      }
    },
    {
      path: '/edit_jiudian',
      name: 'edit_jiudian',
      component: () => import('../components/mysj/edit_jiudian.vue'),
      meta: {
        title: '编辑酒店资料'
      }
    },
    {
      path: '/tongji_detali',
      name: 'tongji_detali',
      component: () => import('../components/mysj/tongji_detali.vue'),
      meta: {
        title: '收入详情'
      }
    },
    {
      path: '/edit_myteam',
      name: 'edit_myteam',
      component: () => import('../components/mysj/edit_myteam.vue'),
      meta: {
        title: '编辑代理资料'
      }
    },
    {
      path: '/yaoqingma',
      name: 'yaoqingma',
      component: () => import('../components/my/yaoqingma.vue'),
      meta: {
        title: '我的邀请码'
      }
    },
    {
      path: '/ruzhu',
      name: 'ruzhu',
      component: () => import('../components/my/ruzhu.vue'),
      meta: {
        title: '入驻申请'
      }
    },
    {
      path: '/reg',
      name: 'reg',
      component: () => import('../components/my/reg.vue'),
      meta: {
        title: '注册会员'
      }
    },
    {
      path: '/tixian_detail',
      name: 'tixian_detail',
      component: () => import('../components/mymoney/tixian_detail.vue'),
      meta: {
        title: '提现详情'
      }
    },
    {
      path: '/myhotel',
      name: 'myhotel',
      component: () => import('../components/my/myhotel.vue'),
      meta: {
        title: '我的酒店'
      }
    },
    {
      path: '/help_detali',
      name: 'help_detali',
      component: () => import('../components/my/help_detali.vue'),
      meta: {
        title: '帮助详情'
      }
    },
    {
      path: '/taocan',
      name: 'taocan',
      component: () => import('../components/my/saomachongdian.vue'),
      meta: {
        title: '扫码充电'
      }
    },
    {
      path: '/youhuiquan',
      name: 'youhuiquan',
      component: () => import('../components/my/youhuiquan.vue'),
      meta: {
        title: '我的优惠券'
      }
    },
    {
      path: '/jilu',
      name: 'jilu',
      component: () => import('../components/jilu.vue'),
      meta: {
        title: '租借记录'
      }
    },
  ]
})



