import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    loading: false,
    userInfo: {
      user_id:3933,//localStorage.getItem('user_id'), //
      openid:localStorage.getItem('openid') //  
    },
    kefu:'',      //  客服电话
  },
  mutations: {
    userUpdate(state, value) {
      state.userInfo.openid = value
    }
  }
})








