<template>
  <div id="app">
    <router-view />
    <van-loading type="spinner" color="white" v-show="$store.state.loading"/>
  </div>
</template>

<script>
import { Loading } from 'vant';
export default {
  name: 'App',
  components: {
    'van-loading': Loading
  },
  created () {
    // console.log(this.$store.state.loading)
  }
}
</script>
<style>
html{
  font-family: "SourceHanSansCN-Normal" !important;
}
*{
  padding:0;
  margin:0;
}
.ell1{
  overflow : hidden; 
  text-overflow: ellipsis; 
  display: -webkit-box; 
  -webkit-line-clamp: 1; 
  -webkit-box-orient: vertical; 
  -webkit-line-clamp:1; 
}
.ell2{
  overflow : hidden; 
  text-overflow: ellipsis; 
  display: -webkit-box; 
  -webkit-line-clamp: 2; 
  -webkit-box-orient: vertical; 
  -webkit-line-clamp:2; 
}
.pd3{
  padding :0 3%;
}
#app {
  width:100% !important;
  background: #FAFAFA;
  font-family: "SourceHanSansCN-Normal" !important;
  /* font-family: "Avenir", Helvetica, Arial, sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#app .van-nav-bar .van-icon {
}
#app .van-nav-bar {
  height: 100px;
  line-height: 100px;
  background-color: #15181d;
  letter-spacing: 2px;
}
#app .van-loading {
  position: fixed;
  z-index: 1000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 16px;
  border-radius: 8px;
  /* background: rgba(0, 0, 0, 0.5); */
}

::-ms-input-placeholder {
  color: #fff;
}
::-webkit-input-placeholder {
  color: #fff;
}
::-moz-placeholder {
  color: #fff;
}
#app .van-hairline--bottom::after {
  border: none;
}
#app .van-hairline--top-bottom::after {
  border: none;
}
.my_deal .van-tab {
  background: #2c4062;
  color: #fff;
}
.my_deal .van-tab--active {
  color: #f44;
}
.wallet .van-tab {
  background: #394b6d;
  color: #fff;
}
/*适配iphone5 320px */
@media screen and (max-width: 320px) {
    .fs-5{font-size: 8pt !important;}
    .pd-5{padding:0 20pt !important;}
    .flex-5{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      /* justify-content: space-between */
    }
    .p-5{
      display: inline-block;
      margin: 3pt 0;
    }
    .mt-5{margin-top:-260px !important;}
}
@keyframes myfirst{
	0% {transform: rotate(0deg);}
	100% {transform: rotate(360deg);}
}
.c9{
  color:#999;
}
.c6{
  color:#666;
}
.c3{
  color:#333;
}
.c13{
  color:#131415;
}
.f13{
  font-size: 26px;
}
.f14{
  font-size: 28px;
}
.f16{
  font-size: 32px;
}
.f20{
  font-size: 40px;
}
.fw{
  font-weight: 600;
}
.acthu:active{
  background: rgba(0, 0, 0, 0.3) !important;
}
.more{
  padding-top:20pt;
  padding-bottom:70pt;
  color:#555;
  text-align: center;
}
.nomore{
  color:#666;
  text-align: center;
  font-size: 26px;
  margin:200px;
}
</style>



